.Slogan__Slogan__xcvpl {
  width: 90%;
  text-align: center;
  text-shadow: 1px 1px 8px #111111;
  padding: 0 4rem;
}

.Slogan__Slogan__xcvpl h1 {
  font-size: 6.5vw;
  line-height: 7.7vw;
  color: #fff;
}

@media (max-width: 767px) {
  .Slogan__Slogan__xcvpl {
    padding: 0;
  }
  .Slogan__Slogan__xcvpl h1 {
    font-size: 15vw;
    line-height: 18vw;
  }
}